import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import { Button, Input } from '@mui/material';

const GoWasmComponent: React.FC = () => {
    const [imagePreview, setImagePreview] = useState("");
    const [qrCodeResult, setQRcodeResult] = useState("");

    const handleImageUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
        const file = event!.target!.files![0];
        if (!file) return;

        const reader = new FileReader();
        reader.onload = async (e) => {
            const content = e.target!.result;
            console.log(content);
            setImagePreview(content as string);
            try {
                const result = window.functions.decodeQR(content); // Call the Go WASM function
                setQRcodeResult(result);
            } catch (error) {
                console.error('Error decoding QR code:', error);
                setQRcodeResult('Failed to decode QR code.');
            }
        };
        reader.readAsDataURL(file);
    };

    return (
        <div>
            <Box sx={{ display: 'flex' }}>
                <Box sx={{ width: '50%' }}>
                    <TextField
                    fullWidth
                    variant="outlined"
                    multiline
                    rows={10}
                        placeholder="Welcome to QR Code Decoder!"
                        value={qrCodeResult}
                    />
                </Box>
                <Box sx={{ width: '50%' }}>
                    {imagePreview && <img src={imagePreview} alt="QR Code" />}
                </Box>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: '1rem' }}>
                <Button
                    variant="contained"
                    component="label"
                >
                    Upload File
                    <input
                        type="file"
                        hidden
                        accept="image/*"
                        onChange={handleImageUpload}
                    />
                </Button>
            </Box>
        </div >
    );
};

export default GoWasmComponent;